.action {
    &-pages {
        display: flex;
        align-items: center;
        a {
            &:first-child {
                margin-right: 26px;
            }
        }
    }
}
.quantity {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    border: 1px solid var(--mainColor);
    width: 26px;
    height: 14px;
    background-color: var(--White);
    font-size: 10px;
    font-family: var(--mainFont);
    line-height: 1.68;
    top: -8px;
    left: -20px;
}

.dropdown {
    position: relative;
    &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 100%;
        height: 5px;
        width: 100%;
    }
    &__list {
        display: none;
        position: absolute;
        width: calc(100% + 10px);
        background-color: var(--White);
        left: -5px;
        top: calc(100% + 5px);
        z-index: 1;
        a {
            display: flex;
            align-items: center;
            padding: 5px;
        }
    }
    &__img, img {
        width: 18px;
        height: 18px;
        border-radius: 50%;
        margin-right: 11px;
        object-fit: cover;
        flex-shrink: 0;
    }
    &__current {
        align-items: center;
    }
    &:hover {
        .dropdown__list {
            display: block;
        }
    }
}
@media(max-width: 450px) {
    .dropdown {
        &__img, img {
            margin-right: 6px;
        }
    }
}

div.social-media {
    ul {
        margin-top: 20px;
    }
    li {
        padding: 0;
        margin-right: 20px;
    }
    i {
        font-size: 15px;
    }
}

.jq-ry-container {
    padding: 0;
}

.fancybox-bg{
    background: rgba(0, 0, 0, .7);
}
.fancybox-slide--html{
    padding: 10px 0;
}
.fancybox-button.fancybox-close-small{
    width: 28px;
    height: 28px;
    padding: 0;
    right: 22px;
    top: 22px;
}
.fancybox-content{
    padding: 0;
    font-family: var(--mainFont);
}
.fancybox-can-pan .fancybox-content, .fancybox-can-swipe .fancybox-content{
    cursor: auto;
}
.modal{
    background-color: var(--White);
    border-radius: 10px;
    &__inner {
        padding: 55px 60px;
        .section-title {
            margin: 0 0 20px;
            text-align: left;
        }
        p {
            font-size: 13px;
            line-height: 1.4;
        }
        .alert {
            margin: 0 0 30px;
            font-size: 13px;
        }
        .input {
            height: 40px;
            border-radius: 15px;
            padding: 5px 10px;
            border: 1px solid var(--Grey);
            flex-grow: 1;
            &-wrap {
                display: flex;
                align-items: center;
                width: 100%;
                margin-bottom: 15px;
            }
            &-name {
                width: 90px;
            }
            &:focus {
                outline: none;
            }
            &-list {
                margin-bottom: 25px;
            }
        }
        .btn-more {
            position: relative;
            display: inline-block;
            margin: 0;
            font-size: 17px;
            text-decoration: none;
            &::after {
                content: "";
                display: block;
                width: 100%;
                height: 1px;
                background-color: var(--mainColor);
                position: absolute;
                left: 0;
                top: 100%;
            }
        }
        .btn-bg {
            padding: 15px 30px;
        }
        &-action {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 25px;
        }
    }
}
#logIn {
    width: 490px;
    display: none;
}
@media(max-width: 600px){
    .fancybox-button.fancybox-close-small {
        right: 8px;
        top: 8px;
    }
    #logIn {
        width: 290px;
        .input-list {
            margin-bottom: 15px;
        }
    }
    .modal{
        &__inner {
            padding: 25px 20px 30px;
            .section-title {
                margin-bottom: 10px;
            }
            .top {
                 p {
                     margin-bottom: 20px;
                 }
            }
            .bottom {
                margin-top: 10px;
                padding-top: 0;
            }
            .input {
                &-wrap {
                    flex-wrap: wrap;
                }
                &-name {
                    margin-bottom: 10px;
                }
            }
            .btn-bg {
                width: 100%;
                text-align: center;
            }
            .btn-more {
                margin: 20px auto 0;
            }
            &-action {
                flex-wrap: wrap;
            }
        }
    }
}